<template>
  <!-- redis列表页 -->
  <div>
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="el-icon-plus" size="small" type="warning" @click="handleAdd()">{{$t('common.add')}}</el-button>
      <div class="inline-block ml-10" v-if="tableVal.length !== 0">
        <el-button icon="fa fa-trash" type="danger" size="small" @click="handleDelete()">{{ $t('common.delete') }}
        </el-button>
        <el-button icon="fa fa-plane" type="info" size="small" @click="installRedis()">部署</el-button>
        <el-button icon="fa fa-play" type="info" size="small" @click="startRedisTo()">启动</el-button>
        <el-button icon="fa fa-link" type="warning" size="small" @click="uninstallRedis()">卸载</el-button>
        <el-button icon="fa fa-stop" type="warning" size="small" @click="stopRedisTo()">停止</el-button>
      </div>
    </div>
    <el-form :model="redisFindForm" ref="redisFindForm" label-width="85px" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="集群名称：">
            <el-select v-model="redisFindForm.clusterId" clearable :placeholder="$t('common.pleaseChoose')">
              <el-option v-for="item in classify.clusterName" :key="item.pkValue" :label="item.pkDesc" :value="item.pkValue">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="实例名称：">
            <el-input type="text" v-model.trim="redisFindForm.name" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="安装路径：">
            <el-input type="text" v-model.trim="redisFindForm.installPath" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button icon="el-icon-search" type="primary" size="small" @click="search()">{{$t('common.search')}}
          </el-button>
        </el-col>
      </el-row>
    </el-form>
    <!-- 表格部分 -->
    <el-table stripe :data="redisList" ref="multipleTable" @selection-change="changeFun" v-loading="loading" style="width: 100%">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <!-- 实例名称 -->
      <el-table-column align="center" prop="name" :label="$t('redis.redisList.name')" width="130">
      </el-table-column>
      <el-table-column align="center" prop="status" :label="$t('common.status')" width="150">
        <template slot-scope="props">
          <span :style="props.row.status | filterAppStatusColor">
            <i v-if="props.row.status !=='uninstall'" class="fa fa-lightbulb-o name-state"></i>
            {{props.row.status | filterAppStatus}}
          </span>
          <el-tooltip content="刷新状态" placement="top">
            <el-button type="text" size="small" @click="refreshStatus(props.$index, props)" icon="fa fa-refresh"></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column align="center" :label="$t('redis.redisList.instType')" width="100">
        <template slot-scope="scope">
          <span>{{scope.row.instType | instType}}<span v-if="scope.row.instType !== '3'">{{scope.row.instTypeStr | instTypeStr}}</span>
            <i class="el-icon-refresh status" @click="refreshStatus(scope.$index,scope)"></i>
          </span>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="clusterName" :label="$t('redis.redisList.clusterName')" width="130">
      </el-table-column>
      <el-table-column align="center" prop="ip" min-width="140" :label="$t('redis.redisList.bindIp')">
      </el-table-column>
      <el-table-column align="center" :label="$t('redis.redisList.config')" width="70">
        <template slot-scope="scope">
          <el-button type="text" size="mini" @click="addConfig(scope)">
            <i class="el-icon-setting"></i>
          </el-button>
        </template>
      </el-table-column>
      <!-- 监控 -->
      <el-table-column align="center" :label="$t('common.monitor')" width="70">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="moniter(scope.row)" icon="fa fa-bar-chart"></el-button>
        </template>
      </el-table-column>
      <el-table-column :label="'日志'" width="100" align="center">
          <template slot-scope="props">
            <el-button type="text" size="mini" icon="fa fa-file-text-o" @click="viewLog(props.row)"></el-button>
          </template>
        </el-table-column>
      <el-table-column align="center" prop="installPath" min-width="140" :label="$t('redis.redisList.installPath')">
      </el-table-column>
      <el-table-column align="center" prop="installName" :label="$t('redis.redisList.installName')" width="130">
      </el-table-column>
      <el-table-column align="center" prop="versionNo" :label="$t('redis.redisList.version')" width="80">
      </el-table-column>
      <el-table-column width="120" align="center" :label="$t('common.operation')" fixed="right">
        <template slot-scope="props">
          <el-dropdown trigger="click">
            <el-button type="text" size="mini">
              操作
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button v-if="props.row.status === 'UNINSTALL'" type="text" size="mini" icon="fa fa-pencil-square-o" @click="handleEdit(props)">修改</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.status === 'UNINSTALL'" type="text" size="mini" icon="fa fa-plane" @click="installRedis(props)">部署</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.status === 'INSTALL' || props.row.status === 'STOPPED'" type="text" size="mini" icon="fa fa-link" @click="uninstallRedis(props)">卸载</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.status === 'INSTALL' || props.row.status === 'STOPPED'" type="text" size="mini" icon="fa fa-play" @click="startRedisTo(props)">启动</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.status === 'RUNNING' || props.row.status === 'INSTALL'" type="text" size="mini" icon="fa fa-stop" @click="stopRedisTo(props)">停止</el-button>
              </el-dropdown-item>
              <el-dropdown-item divided v-if="props.row.status === 'UNINSTALL'">
                <el-button type="text" size="mini" class="btn-text-red" @click="handleDelete(props)" icon="fa fa-trash">删除</el-button>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination background @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize" layout="total, prev, pager, next, jumper" :total="totalPage">
      </el-pagination>
    </div>
    <!-- 添加/更新弹框 -->
    <el-dialog :title="way" :visible.sync="addVisible" width="35%" center>
      <el-form ref="redisForm" :model="redisForm" :rules="configRules" status-icon label-width="100px" size="small">
        <el-form-item v-if="way === '添加redis实例'">
          <el-radio-group v-model="redisForm.method">
            <el-radio label="UNINSTALL">{{$t('redis.redisList.uninstall')}}</el-radio>
            <el-radio label="INSTALL">{{$t('redis.redisList.install')}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.name')" prop="name">
          <el-input v-model.trim="redisForm.name" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.clusterName')">
          <el-select class="block" v-model="redisForm.clusterId" :placeholder="$t('common.pleaseChoose')">
            <el-option v-for="item in classify.clusterName" :key="item.pkValue" :label="item.pkDesc" :value="item.pkValue">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.ipPath')" prop="ip">
          <el-select class="block" v-model="redisForm.ip" :placeholder="$t('common.pleaseChoose')" :disabled="disModify">
            <el-option v-for="item in classify.ipGroup" :key="item.id" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.instType')" prop="instType">
          <el-select class="block" v-model="redisForm.instType" :placeholder="$t('common.pleaseChoose')" :disabled="disModify">
            <el-option v-for="item in classify.claType" :key="item.pkValue" :label="item.pkDesc" :value="item.pkValue">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.installVersion')" prop="versionNo">
          <el-select class="block" v-model="redisForm.versionNo" :placeholder="$t('common.pleaseChoose')" :disabled="disModify">
            <el-option v-for="item in classify.claVersion" :key="item.pkValue" :label="item.pkDesc" :value="item.pkValue">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.installPath')" prop="installPath">
          <el-input v-model.trim="redisForm.installPath" maxlength="200" :disabled="disModify"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.installName')" prop="installName">
          <el-input v-model.trim="redisForm.installName" maxlength="50" :disabled="disModify"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveAdd" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="addVisible = false" size="small">{{$t('common.cancel')}}</el-button>
      </span>
    </el-dialog>
    <!-- 配置输入框 -->
    <el-dialog :title="configName" :visible.sync="configFormVisible" width="35%" center :before-close="handleClose">
      <el-form :model="configForm" :rules="configRules" label-width="180px" size="small" status-icon>
        <!-- 主的配置 -->
        <el-form-item :label="$t('redis.redisList.port')+'：'" v-if="configFirst" prop="port">
          <el-input v-model.trim="configForm.port" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.ipPath')+'：'" v-if="configFirst" prop="ip">
          <el-input v-model.trim="configForm.ip" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.dataName')+'：'" v-if="configFirst">
          <el-input v-model.trim="configForm.dbFileName" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.dataPath')+'：'" v-if="configFirst">
          <el-input v-model.trim="configForm.dbDir" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.maxMemo')+'：'" v-if="configFirst">
          <el-input v-model.trim="configForm.maxMemory" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.maxConnect')+'：'" v-if="configFirst">
          <el-input v-model.trim="configForm.maxClient" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <!-- 从的配置 -->
        <el-form-item :label="$t('redis.redisList.readType')+'：'" v-if="configSecond">
          <el-input v-model.trim="configForm.slaveReadOnly" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.masterIp')+'：'" v-if="configSecond">
          <el-input v-model.trim="configForm.masterIp" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.masterPort')+'：'" v-if="configSecond">
          <el-input v-model.trim="configForm.masterPort" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <!-- 哨兵的配置 -->
        <el-form-item :label="$t('redis.redisList.portNum')+'：'" v-if="configThird">
          <el-input v-model.trim="configForm.port" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.redisPath')+'：'" v-if="configThird">
          <el-input v-model.trim="configForm.masterIp" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.redisIp')+'：'" v-if="configThird">
          <el-input v-model.trim="configForm.masterPort" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.lessVote')+'：'" v-if="configThird">
          <el-input v-model.trim="configForm.quorum" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.resTimeLimit')+'：'" v-if="configThird">
          <el-input v-model.trim="configForm.downAfterTime" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item :label="$t('redis.redisList.priLimit')+'：'" v-if="configThird">
          <el-input v-model.trim="configForm.failoverTimeout" auto-complete="off" :disabled="configDisabled"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="configTrue()" size="small" :disabled="configDisabled">{{$t('common.ok')}}</el-button>
        <el-button @click="configFalse()" size="small">{{$t('common.cancel')}}</el-button>
      </div>
    </el-dialog>
    <redis-log v-if="logVisible" :logParams="logParams" />
  </div>
</template>
<script>
// js部分
import { mapState, mapActions } from 'vuex'
import redisLog from './redis-log'

export default {
  components: {
    redisLog
  },
  data() {
    return {
      idx: 0,
      currentPage: 1, // 当前页数
      totalPage: 10, // 总页数
      pageSize: 10,
      disModify: false,
      loading: true,
      // 查询模板
      redisFindForm: {
        name: '',
        installPath: '',
        clusterId: ''
      },
      // 获得表格多选按钮
      tableVal: [],
      // 模式--添加和编辑add/edit
      way: '',
      // 添加框
      addVisible: false,
      // 添加模板
      redisForm: {
        method: 'UNINSTALL'
      },
      // 配置主
      configFirst: false,
      // 配置从
      configSecond: false,
      // 配置哨兵
      configThird: false,
      // 配置框
      configFormVisible: false,
      // 配置规则
      configRules: {
        name: [
          { required: true, message: '请输入实例名称', trigger: 'blur' }
        ],
        ip: [
          { required: true, message: '请输入ip', trigger: 'blur' }
        ],
        port: [
          { required: true, message: '请输入端口', trigger: 'blur' }
        ],
        instType: [
          { required: true, message: '请输入实例类型', trigger: 'blur' }
        ],
        versionNo: [
          { required: true, message: '请输入版本号', trigger: 'blur' }
        ],
        installPath: [
          { required: true, message: '请输入安装路径', trigger: 'blur' }
        ],
        installName: [
          { required: true, message: '请输入安装名', trigger: 'blur' }
        ]
      },
      configName: '',
      // 配置模板
      configForm: {},
      logVisible: false,
      logParams: {},
      configDisabled: false
    }
  },
  computed: {
    ...mapState({
      redisList: state => state.redis.redisList,
      classify: state => state.redis.classify
    })
  },
  activated() {
    const vm = this
    vm.redisFindForm.clusterId = vm.$route.params.clusterId !== undefined ? vm.$route.params.clusterId : vm.redisFindForm.clusterId
    vm.search()
    vm.findRedisCombox(vm)
  },
  methods: {
    ...mapActions('redis', ['findRedisInst', 'findRedisCombox', 'addRedisInst', 'updateRedisInst', 'deleteRedisInst', 'deployRedis', 'cleanRedis', 'startRedis', 'stopRedis', 'updateRedisConf', 'getRedisStatus']),
    // 翻页事件
    handleCurrentChange(val) {
      this.currentPage = val
      this.search()
    },
    // 查询
    search() {
      this.loading = true
      const vm = this
      vm.redisIntParam = Object.assign({
        start: vm.currentPage,
        length: vm.pageSize
      }, vm.redisFindForm)
      vm.findRedisInst(vm)
    },
    // 添加
    handleAdd() {
      this.way = '添加redis实例'
      this.disModify = false
      this.redisForm = {
        method: 'UNINSTALL'
      }
      this.addVisible = true
    },
    // 修改
    handleEdit(props) {
      this.disModify = true
      this.way = '修改redis实例'
      let { moId, name, clusterId, ip, instType, versionNo, installPath, installName } = JSON.parse(JSON.stringify(props.row))
      clusterId = clusterId + ''
      this.redisForm = { moId, name, clusterId, ip, instType, versionNo, installPath, installName }
      this.addVisible = true
    },
    // 添加/编辑 -确定
    saveAdd() {
      const vm = this
      vm.$refs['redisForm'].validate(valid => {
        if (valid) {
          if (vm.way === '添加redis实例') {
            vm.addRedisInst(vm)
          }
          if (vm.way === '修改redis实例') {
            vm.updateRedisInst(vm)
          }
          this.addVisible = false
        }
      })
    },
    // 配置添加
    addConfig(scope) {
      this.configName = scope.row.name + '的配置信息'
      this.configFormVisible = true
      if (scope.row.instType === '1') {
        console.log('主')
        this.configFirst = true
      } else if (scope.row.instType === '2') {
        console.log('从')
        this.configFirst = true
        this.configSecond = true
      } else if (scope.row.instType === '3') {
        console.log('哨兵')
        this.configThird = true
      }
      if (scope.row.status !== 'UNINSTALL') {
        this.configDisabled = true
      } else {
        this.configDisabled = false
      }
      this.configForm = { ...scope.row }
    },
    // 配置点击
    handleClose() {
      this.configFirst = false
      this.configSecond = false
      this.configThird = false
      this.configFormVisible = false
    },
    // 配置确定
    configTrue() {
      const vm = this
      vm.configFirst = false
      vm.configSecond = false
      vm.configThird = false
      vm.configFormVisible = false
      vm.updateRedisConf(vm)
    },
    // 配置取消
    configFalse() {
      this.configFirst = false
      this.configSecond = false
      this.configThird = false
      this.configFormVisible = false
    },
    // 拿到表格选中的值
    changeFun(val) {
      this.tableVal = val
    },
    // 删除功能
    handleDelete(props = {}) {
      this.operationList(props, 'deleteRedisInst', '删除')
    },
    // 部署
    installRedis(props = {}) {
      this.operationList(props, 'deployRedis', '部署')
    },
    // 卸载
    uninstallRedis(props = {}) {
      this.operationList(props, 'cleanRedis', '卸载')
    },
    // 启动
    startRedisTo(props = {}) {
      this.operationList(props, 'startRedis', '启动')
    },
    // 停止
    stopRedisTo(props = {}) {
      this.operationList(props, 'stopRedis', '停止')
    },
    operationList(props, method, opText) {
      const vm = this
      vm.conmmonMethods.makePopBox(vm, opText).then(() => {
        if (props.row && props.row.moId) {
          vm.redisForm.moId = props.row.moId
          vm[method](vm)
        } else {
          let ids = []
          const nameList = []
          vm.tableVal.forEach(item => {
            ids.push(item.moId)
            if (opText === '删除' && item.status !== 'UNINSTALL') {
              nameList.push(item.name)
            }
            if (opText === '部署' && item.status !== 'UNINSTALL') {
              nameList.push(item.name)
            }
            if (opText === '启动' && (item.status === 'RUNNING' || item.status === 'UNINSTALL')) {
              nameList.push(item.name)
            }
            if (opText === '停止' && (item.status === 'STOPPED' || item.status === 'UNINSTALL')) {
              nameList.push(item.name)
            }
            if (opText === '卸载' && (item.status === 'UNINSTALL' || item.status === 'RUNNING')) {
              nameList.push(item.name)
            }
          })
          if (nameList.length > 0) {
            let nameStr = nameList.join('、')
            vm.$message({
              type: 'warning',
              message: `实例名称：${nameStr}不符合该操作，请重新选择`,
              duration: 5000
            })
            return
          }
          vm.redisForm.moId = ids.toString()
          vm[method](vm)
        }
      })
    },
    // 监控
    moniter(value) {
      if (!value) {
        return
      }
      let query = {
        ip: value.ip,
        port: value.port
      }
      this.$router.push({name: 'redisEcharts', query: query, params: {'id': value.ip}})
    },
    // 日志
    showRedisLog(scope) {
      this.$router.push({
        name: 'redisLog',
        params: scope.row
      })
    },
    // 刷新
    refreshStatus(index, scope) {
      const vm = this
      vm.idx = index
      vm.redisForm.moId = scope.row.moId
      vm.getRedisStatus(vm)
    },
    viewLog(props) {
      this.logParams = props
      this.logVisible = true
    }
  }
}
</script>

<style type="text/scss" lang="scss" scoped>
.status {
  cursor: pointer;
}
</style>



