<template>
  <div class="log-modal">
    <el-dialog
      :title="logParams.name + '日志'"
      :visible.sync="$parent.logVisible"
      width="60%"
      center
      @close="closeLog">
      <div class="log-box" v-loading="loading" element-loading-background="rgba(0, 0, 0, 0)">
        <p v-for="(item, index) in redisLog" v-text="item" :key="index"></p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  export default {
    data() {
      return {
        loading: false
      }
    },
    props: {
      logParams: Object
    },
    computed: {
      ...mapState({
        redisLog: state => state.redis.redisLog
      })
    },
    created() {
      this.getRedisLog([this, {moId: this.logParams.moId, lines: 50}])
    },
    methods: {
      ...mapActions('redis', ['getRedisLog']),
      closeLog() {
        this.$parent.logVisible = false
      }
    }
  }
</script>



